import React from "react";
import { Favorite, FavoriteBorder } from "@mui/icons-material";
import {
  Card,
  CardMedia,
  Checkbox,
  IconButton,
  Typography,
} from "@mui/material";
import BedIcon from "@mui/icons-material/Bed";
import BathtubIcon from "@mui/icons-material/Bathtub";
import WorkspacesIcon from "@mui/icons-material/Workspaces";
import "./index.css";

const CardComponent = (props) => {
  const { id, title, discription, price, rooms, bathrooms, area, image } =
    props;
  return (
    <div className="cards" key={id}>
      <Card sx={{ margin: 4, width: "320px", borderRadius: "5px" }}>
        <CardMedia
          component="img"
          height="180"
          image={
            image ||
            "https://images.unsplash.com/photo-1444418776041-9c7e33cc5a9c"
          }
          alt="Paella dish"
        />
        <div className="card-head">
          <div className="card-title">
            <Typography variant="h6" color="rgb(113, 84, 243)">
              ${price}/month
            </Typography>
            <IconButton aria-label="add to favorites">
              <Checkbox
                icon={<FavoriteBorder />}
                checkedIcon={<Favorite sx={{ color: "red" }} />}
              />
            </IconButton>
          </div>
          <div className="title-head">
            <Typography variant="h5" sx={{ fontWeight: "bold" }}>
              {title}
            </Typography>
            <Typography
              variant="p"
              color="rgba(83, 81, 81, 0.753)"
              sx={{ fontWeight: "600" }}
            >
              {discription}
            </Typography>
          </div>
          <div className="card-rooms">
            <p>
              <BedIcon className="icons" />
              {rooms}Rooms
            </p>
            <p>
              <BathtubIcon className="icons" />
              {bathrooms}Bathrooms
            </p>
            <p>
              <WorkspacesIcon className="icons" />
              {area}
            </p>
          </div>
        </div>
      </Card>
    </div>
  );
};

export default CardComponent;
